.ExampleContent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 450px;
    padding: 0 30px;
}
.ExampleContent-section {
    -ms-flex: 0 1 50%;
        flex: 0 1 50%;
    padding: 30px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.ExampleContent-element {
    display: inline-block;
    position: relative;
    padding: 40px 60px;
    background-color: #ff007f;
    font-size: .8em;
    border-radius: 3px;
    margin-bottom: 20px;
    color: white;
    white-space: nowrap;
}

.ExampleContent-element:after {
    content: " ";
    position: absolute;
    border: 5px solid #ff007f;
    width: 0;
    margin-top: -5px;
    margin-left: -5px;
    left: 50%;
    top: 100%;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.ExampleContent-animated {
  -webkit-animation-name: animated;
          animation-name: animated;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes animated {
  0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
  50% { -webkit-transform: perspective(500px) translate3d(0, -10px, 20px); transform: perspective(500px) translate3d(0, -10px, 20px); }
  100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@keyframes animated {
  0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
  50% { -webkit-transform: perspective(500px) translate3d(0, -10px, 20px); transform: perspective(500px) translate3d(0, -10px, 20px); }
  100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}



