body {
  margin: 0;
  text-align: center;
  padding: 0;
  font-family: sans-serif;
  font-size: 20px;
  color: #212121;
  font-family: -apple-system-body,BlinkMacSystemFont,SFUI,HelveticaNeue,Helvetica,Arial,sans-serif;
}
a {
  color: #ff007f;
  text-decoration: none;
  border-bottom: 1px dashed rgba(255, 0, 127, .4);
}

a:hover {
  border-bottom-style: solid;
}

p {
  font-size: .8em;
  line-height: 1.9;
  margin-bottom: 1.4em;
}

small {
  font-size: .6em;
  opacity: 0.5;
}

h1 {
  margin-top: 0;
  font-weight: 500;
}

h2 {
  font-size: 1em;
  font-weight: 500;
}

pre {
    width: 150%;
    margin-left: -25%;
    font-size: 1em;
}

pre,
code {
  color: #17d382;
}


@media (max-width: 850px) {
    pre {
      width: 100%;
      overflow-x: scroll;
      margin-left: 0;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
    }
}



