.AnimatedText {
    position: relative;
}

.AnimatedText:hover .AnimatedText-button {
    opacity: 1;
}
.AnimatedText:hover .AnimatedText-button:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

.AnimatedText-button {
    position: absolute;
    right: 0;
    top: 0;
    background: none;
    opacity: .3;
    color: white;
    font-size: 1.2em;
    border: 0;
    outline: none;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.AnimatedText-animating {
    -webkit-animation-name: expandText;
            animation-name: expandText;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

@-webkit-keyframes expandText {
    50% {
        opacity: 0.7;
        letter-spacing: 3px;
    }
}

@keyframes expandText {
    50% {
        opacity: 0.7;
        letter-spacing: 3px;
    }
}



